import React, { useState } from "react";
import { Form, Button } from "react-bootstrap";
import "bootstrap/dist/css/bootstrap.min.css";
const Forms = () => {
  const [ner, sner] = useState("");
  const [eer, seer] = useState("");
  const [per, sper] = useState("");
  const validation = (e, type) => {
    e.preventDefault();
    const val = e.target.value;
    if (type === "name") {
      if (val.trim() === "") {
        sner("name required");
      } else if (val.match(/[a-zA-z]/g)) {
        sner("");
      } else {
        sner("Letter reuired");
      }
    } else if (type === "email") {
      if (val.trim() === "") {
        seer("email required");
      } else if (val.indexOf("@")) {
        seer("Please add @");
      }
    } else if (type === "password") {
      if (val.trim() === "") {
        sper("password required");
      } else if (val.match(/[a-zA-Z1-9@#$%^&*_]/g)) {
        sper("Please add @");
      }
    }
    console.log(val);
  };
  return (
    <Form
      style={{
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        width: `calc(100% - 65%)`,
        marginLeft: `calc(100% - 70%)`,
        flexWrap: "wrap"
      }}
    >
      <Form.Group style={{ width: "100%" }}>
        <Form.Label>Name</Form.Label>
        {ner ? <span className="text-danger mt-2">{ner}</span> : ""}
        <Form.Control
          type="text"
          placeholder="Please enter name"
          onChange={(e) => validation(e, "name")}
        />
        <Form.Label>Email</Form.Label>
        {eer ? <span className="text-danger">{eer}</span> : ""}
        <Form.Control
          type="email"
          placeholder="Please enter email"
          onChange={(e) => validation(e, "email")}
        />

        <Form.Label>Password</Form.Label>
        {per ? <span className="text-danger">{per}</span> : ""}
        <Form.Control
          type="password"
          placeholder="Please enter password"
          onChange={(e) => validation(e, "password")}
        />
      </Form.Group>
      <Button className="mt-2">Submit</Button>
    </Form>
  );
};

export default Forms;
